const getRelativeTimestamp = (timestamp) => {
  let relativeTime = Math.floor(( Date.now() - timestamp ) / 1000)
  const sec = relativeTime % 60
  relativeTime /= 60
  if (relativeTime < 1) return `${Math.floor(sec)}`
  const min = relativeTime % 60
  relativeTime /= 60
  if (relativeTime < 1) return `${Math.floor(min)}`
  const hour = relativeTime % 24
  relativeTime /= 24
  if (relativeTime < 1) return `${Math.floor(hour)}`
  if (relativeTime < 30) return `${Math.floor(relativeTime)}`
  if (relativeTime < 365) return `${Math.floor(relativeTime / 30)}`
  return `${Math.floor(relativeTime / 365)}`
}

export default getRelativeTimestamp