import React, {Component} from 'react'
import {connect} from 'react-redux'
import PostsList from '../stateless_component/PostsList'
import Comet from './Comet'
import { 
  handleGetHomePosts,
  handleGetNewPublish,
  initLastFetch
} from '../../actions/blog'

class Home extends Component {
  constructor (props) {
    super(props)
    this._handleGetNewPublish = this.props.handleGetNewPublish.bind(null, this.props.handleGetHomePosts)
  }

  componentDidMount () {
    const timeline = this.props.homeTimeline.timeline
    if (timeline.length === 0) {
      this.props.handleGetHomePosts()
    } else {
      const posts = this.props.posts
      const top = timeline[0]
      const bottom = timeline[timeline.length-1]
      this.props.initLastFetch(posts.entities[top].created_at, posts.entities[bottom].created_at)
    }
  }

  render () {
    const {
      timeline,
      loading,
      isLoadingDirection
    } = this.props.homeTimeline
    return (
      <div className='Home'>
        <Comet
          handleGetNewPublish={this._handleGetNewPublish}/>
        <PostsList
          isLoadingDirection={isLoadingDirection}
          loading={loading}
          handleGetRequest={this.props.handleGetHomePosts}
          itemHeight={250}
          timeline={timeline}
          posts={this.props.posts}
        />
      </div>
    )
  }
} 

const mapStateToProps = (state) => ({ 
  homeTimeline: state.blog.homeTimeline,
  posts: state.blog.entities.posts,
})
const mapDispatchToProps = (dispatch) => ({
  handleGetHomePosts: (...args) => dispatch(handleGetHomePosts(...args)),
  handleGetNewPublish: (...args) => dispatch(handleGetNewPublish(...args)),
  initLastFetch: (...args) => dispatch(initLastFetch(...args))
})

const ConnectedComponent = connect(mapStateToProps, mapDispatchToProps)(Home)

export default ConnectedComponent